"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { createClient } from "@/lib/supabase/client"
import { Plus, Edit2, Trash2, QrCode, Users, TrendingUp, AlertCircle, CheckCircle, Clock } from "lucide-react"

type TableStatus = 'available' | 'occupied' | 'reserved'

export default function TablesPage() {
  const router = useRouter()
  const supabase = createClient()
  const [loading, setLoading] = useState(true)
  const [tables, setTables] = useState<any[]>([])
  const [showModal, setShowModal] = useState(false)
  const [editingTable, setEditingTable] = useState<any>(null)
  const [restaurant, setRestaurant] = useState<any>(null)
  const [showQRModal, setShowQRModal] = useState(false)
  const [selectedQRTable, setSelectedQRTable] = useState<any>(null)
  const [tableAnalytics, setTableAnalytics] = useState<any>({})
  
  const [formData, setFormData] = useState({
    number: '',
    capacity: ''
  })

  useEffect(() => {
    loadTables()
    loadTableAnalytics()
  }, [])

  async function loadTables() {
    setLoading(true)
    try {
      const { data: { user } } = await supabase.auth.getUser()
      if (!user) {
        router.push('/login')
        return
      }

      // Get selected restaurant from localStorage
      const selectedRestaurantId = localStorage.getItem('selected_restaurant_id')
      if (!selectedRestaurantId) {
        router.push('/dashboard')
        return
      }

      const { data: restaurants } = await supabase
        .from('restaurants')
        .select('*')
        .eq('id', selectedRestaurantId)
        .eq('owner_id', user.id)
        .single()

      if (!restaurants) return
      setRestaurant(restaurants)

      const { data: tablesData, error: tablesError } = await supabase
        .from('tables')
        .select('*')
        .eq('restaurant_id', restaurants.id)
        .order('number')

      console.log('Tables data:', tablesData)
      console.log('Tables error:', tablesError)
      console.log('Restaurant ID:', restaurants.id)

      if (tablesData) {
        setTables(tablesData)
      }
    } catch (error) {
      console.error('Error loading tables:', error)
    } finally {
      setLoading(false)
    }
  }

  async function loadTableAnalytics() {
    try {
      const selectedRestaurantId = localStorage.getItem('selected_restaurant_id')
      if (!selectedRestaurantId) return

      // Get all orders with table information
      const { data: orders } = await supabase
        .from('orders')
        .select('*, tables(id, number)')
        .eq('restaurant_id', selectedRestaurantId)
        .order('created_at', { ascending: false })

      if (!orders) return

      // Calculate statistics per table
      const analytics: any = {}
      
      orders.forEach((order: any) => {
        if (!order.tables) return
        
        const tableId = order.tables.id
        const tableNumber = order.tables.number
        
        if (!analytics[tableId]) {
          analytics[tableId] = {
            tableId,
            tableNumber,
            totalOrders: 0,
            totalRevenue: 0,
            lastOccupied: null,
            occupiedDuration: 0
          }
        }
        
        analytics[tableId].totalOrders++
        analytics[tableId].totalRevenue += parseFloat(order.total_amount || 0)
        
        // Track last occupied time
        if (order.created_at) {
          const orderTime = new Date(order.created_at)
          if (!analytics[tableId].lastOccupied || orderTime > new Date(analytics[tableId].lastOccupied)) {
            analytics[tableId].lastOccupied = order.created_at
          }
        }
      })

      setTableAnalytics(analytics)
    } catch (error) {
      console.error('Error loading table analytics:', error)
    }
  }

  function openModal(table: any = null) {
    if (table) {
      setEditingTable(table)
      setFormData({
        number: table.number || '',
        capacity: table.capacity?.toString() || ''
      })
    } else {
      setEditingTable(null)
      setFormData({
        number: '',
        capacity: ''
      })
    }
    setShowModal(true)
  }

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault()
    
    if (!restaurant) return

    const tableData: any = {
      number: formData.number,
      capacity: parseInt(formData.capacity),
      restaurant_id: restaurant.id
    }

    // Generate barcode for new tables
    if (!editingTable) {
      tableData.barcode = `TABLE-${formData.number}-${Math.random().toString(36).substring(2, 10).toUpperCase()}`
    }

    try {
      if (editingTable) {
        await supabase
          .from('tables')
          .update(tableData)
          .eq('id', editingTable.id)
      } else {
        await supabase
          .from('tables')
          .insert(tableData)
      }

      setShowModal(false)
      loadTables()
    } catch (error) {
      console.error('Error saving table:', error)
    }
  }

  async function deleteTable(id: string) {
    if (!confirm('Hapus meja ini?')) return

    try {
      await supabase
        .from('tables')
        .delete()
        .eq('id', id)

      loadTables()
    } catch (error) {
      console.error('Error deleting table:', error)
    }
  }

  async function updateTableStatus(id: string, status: TableStatus) {
    try {
      await supabase
        .from('tables')
        .update({ status })
        .eq('id', id)

      loadTables()
    } catch (error) {
      console.error('Error updating table status:', error)
    }
  }

  async function showQRCode(table: any) {
    let tableToShow = table
    
    // Generate barcode if table doesn't have one
    if (!table.barcode) {
      const barcode = `TABLE-${table.number}-${Math.random().toString(36).substring(2, 10).toUpperCase()}`
      const { error } = await supabase
        .from('tables')
        .update({ barcode })
        .eq('id', table.id)
      
      if (!error) {
        tableToShow = { ...table, barcode }
        // Update tables list
        setTables(prevTables => 
          prevTables.map(t => t.id === table.id ? tableToShow : t)
        )
      }
    }
    
    setSelectedQRTable(tableToShow)
    setShowQRModal(true)
  }

  const getStatusColor = (status: TableStatus) => {
    switch (status) {
      case 'available': return 'bg-green-100 text-green-800 border-green-200'
      case 'occupied': return 'bg-red-100 text-red-800 border-red-200'
      case 'reserved': return 'bg-yellow-100 text-yellow-800 border-yellow-200'
    }
  }

  const getStatusText = (status: TableStatus) => {
    switch (status) {
      case 'available': return 'Tersedia'
      case 'occupied': return 'Terisi'
      case 'reserved': return 'Dipesan'
    }
  }

  const availableTables = tables.filter(t => !t.status || t.status === 'available').length
  const occupiedTables = tables.filter(t => t.status && t.status !== 'available').length

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-[var(--color-accent)]"></div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <header className="bg-white border-b border-gray-200 shadow-sm">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-2xl font-bold text-gray-900">Kelola Meja</h1>
              <p className="text-sm text-gray-500 mt-1">Atur tata letak dan status meja</p>
            </div>
            <button
              onClick={() => openModal()}
              className="flex items-center gap-2 px-4 py-2 bg-[var(--color-accent)] text-white rounded-lg font-medium hover:bg-[var(--color-accent-hover)] transition-colors"
            >
              <Plus className="w-5 h-5" />
              Tambah Meja
            </button>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Table Stats */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
          <div className="bg-white rounded-2xl p-6 border border-gray-100 shadow-sm">
            <h3 className="text-3xl font-bold text-gray-900 mb-1">{tables.length}</h3>
            <p className="text-sm text-gray-600">Total Meja</p>
          </div>
          <div className="bg-white rounded-2xl p-6 border border-gray-100 shadow-sm">
            <h3 className="text-3xl font-bold text-green-600 mb-1">{availableTables}</h3>
            <p className="text-sm text-gray-600">Tersedia</p>
          </div>
          <div className="bg-white rounded-2xl p-6 border border-gray-100 shadow-sm">
            <h3 className="text-3xl font-bold text-red-600 mb-1">{occupiedTables}</h3>
            <p className="text-sm text-gray-600">Terisi</p>
          </div>
        </div>

        {/* Insights & Statistics */}
        {tables.length > 0 && (
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-8">
            {/* Occupancy Rate */}
            <div className="bg-gradient-to-br from-blue-50 to-indigo-50 rounded-2xl p-6 border border-blue-100">
              <div className="flex items-start justify-between mb-4">
                <div>
                  <h3 className="text-lg font-bold text-gray-900 mb-1">Tingkat Okupansi</h3>
                  <p className="text-sm text-gray-600">Persentase meja terisi saat ini</p>
                </div>
                <div className="bg-blue-100 p-3 rounded-xl">
                  <TrendingUp className="w-6 h-6 text-blue-600" />
                </div>
              </div>
              <div className="flex items-end gap-2 mb-3">
                <span className="text-4xl font-bold text-blue-600">
                  {tables.length > 0 ? Math.round((occupiedTables / tables.length) * 100) : 0}%
                </span>
                <span className="text-sm text-gray-600 mb-2">
                  ({occupiedTables} dari {tables.length} meja)
                </span>
              </div>
              <div className="w-full bg-blue-200 rounded-full h-3 overflow-hidden">
                <div 
                  className="bg-blue-600 h-full rounded-full transition-all duration-500"
                  style={{ width: `${tables.length > 0 ? (occupiedTables / tables.length) * 100 : 0}%` }}
                ></div>
              </div>
            </div>

            {/* Total Capacity */}
            <div className="bg-gradient-to-br from-purple-50 to-pink-50 rounded-2xl p-6 border border-purple-100">
              <div className="flex items-start justify-between mb-4">
                <div>
                  <h3 className="text-lg font-bold text-gray-900 mb-1">Total Kapasitas</h3>
                  <p className="text-sm text-gray-600">Jumlah maksimal tamu</p>
                </div>
                <div className="bg-purple-100 p-3 rounded-xl">
                  <Users className="w-6 h-6 text-purple-600" />
                </div>
              </div>
              <div className="flex items-end gap-2 mb-2">
                <span className="text-4xl font-bold text-purple-600">
                  {tables.reduce((sum, t) => sum + (t.capacity || 0), 0)}
                </span>
                <span className="text-sm text-gray-600 mb-2">orang</span>
              </div>
              <p className="text-sm text-gray-600">
                Rata-rata {tables.length > 0 ? (tables.reduce((sum, t) => sum + (t.capacity || 0), 0) / tables.length).toFixed(1) : 0} orang per meja
              </p>
            </div>
          </div>
        )}

        {/* Smart Suggestions */}
        {tables.length > 0 && (
          <div className="mb-8 space-y-3">
            {/* QR Code Reminder */}
            {tables.some(t => !t.barcode) && (
              <div className="bg-amber-50 border-l-4 border-amber-400 rounded-xl p-4 flex items-start gap-3">
                <AlertCircle className="w-5 h-5 text-amber-600 mt-0.5 flex-shrink-0" />
                <div>
                  <h4 className="font-semibold text-amber-900 mb-1">QR Code Belum Lengkap</h4>
                  <p className="text-sm text-amber-800">
                    {tables.filter(t => !t.barcode).length} meja belum memiliki QR code. 
                    Klik tombol QR pada meja untuk generate dan cetak QR code agar pelanggan bisa pesan langsung.
                  </p>
                </div>
              </div>
            )}

            {/* High Occupancy Alert */}
            {tables.length > 0 && (occupiedTables / tables.length) >= 0.8 && (
              <div className="bg-red-50 border-l-4 border-red-400 rounded-xl p-4 flex items-start gap-3">
                <AlertCircle className="w-5 h-5 text-red-600 mt-0.5 flex-shrink-0" />
                <div>
                  <h4 className="font-semibold text-red-900 mb-1">Kapasitas Hampir Penuh</h4>
                  <p className="text-sm text-red-800">
                    {Math.round((occupiedTables / tables.length) * 100)}% meja terisi. Pertimbangkan untuk menambah meja atau siapkan waiting list.
                  </p>
                </div>
              </div>
            )}

            {/* Low Occupancy Tip */}
            {tables.length > 0 && (occupiedTables / tables.length) < 0.3 && occupiedTables > 0 && (
              <div className="bg-blue-50 border-l-4 border-blue-400 rounded-xl p-4 flex items-start gap-3">
                <Clock className="w-5 h-5 text-blue-600 mt-0.5 flex-shrink-0" />
                <div>
                  <h4 className="font-semibold text-blue-900 mb-1">Waktu Sepi Terdeteksi</h4>
                  <p className="text-sm text-blue-800">
                    Hanya {Math.round((occupiedTables / tables.length) * 100)}% meja terisi. Manfaatkan waktu ini untuk persiapan atau promosi happy hour!
                  </p>
                </div>
              </div>
            )}

            {/* All Tables Free */}
            {occupiedTables === 0 && tables.length > 0 && (
              <div className="bg-green-50 border-l-4 border-green-400 rounded-xl p-4 flex items-start gap-3">
                <CheckCircle className="w-5 h-5 text-green-600 mt-0.5 flex-shrink-0" />
                <div>
                  <h4 className="font-semibold text-green-900 mb-1">Semua Meja Tersedia</h4>
                  <p className="text-sm text-green-800">
                    Semua meja siap menerima tamu. Pastikan QR code sudah terpasang di setiap meja untuk memudahkan pemesanan.
                  </p>
                </div>
              </div>
            )}

            {/* First Time Setup */}
            {tables.length === 0 && (
              <div className="bg-blue-50 border-l-4 border-blue-400 rounded-xl p-4 flex items-start gap-3">
                <AlertCircle className="w-5 h-5 text-blue-600 mt-0.5 flex-shrink-0" />
                <div>
                  <h4 className="font-semibold text-blue-900 mb-1">Mulai Tambahkan Meja</h4>
                  <p className="text-sm text-blue-800 mb-3">
                    Belum ada meja yang terdaftar. Tambahkan meja pertama Anda untuk mulai menerima pesanan digital.
                  </p>
                  <ul className="text-sm text-blue-800 space-y-1 ml-4 list-disc">
                    <li>Masukkan nomor meja (contoh: 1, A1, VIP-1)</li>
                    <li>Tentukan kapasitas tempat duduk</li>
                    <li>Generate QR code untuk setiap meja</li>
                    <li>Cetak dan tempel QR code di meja</li>
                  </ul>
                </div>
              </div>
            )}
          </div>
        )}

        {/* Tables Grid */}
        <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
          {tables.length === 0 ? (
            <div className="col-span-full bg-white rounded-2xl p-12 text-center border border-gray-100">
              <p className="text-gray-500 text-lg">Belum ada meja</p>
            </div>
          ) : (
            tables.map((table) => {
              const isOccupied = table.status && table.status !== 'available'
              return (
                <div
                  key={table.id}
                  className={`bg-white rounded-2xl overflow-hidden shadow-sm hover:shadow-md transition-all ${
                    isOccupied 
                      ? 'border-2 border-green-200 bg-green-50' 
                      : 'border border-gray-200'
                  }`}
                >
                  {/* Card Body */}
                  <div className="p-6 text-center">
                    <div className={`w-14 h-14 mx-auto rounded-full flex items-center justify-center mb-3 border-2 ${
                      isOccupied
                        ? 'bg-blue-50 border-blue-200'
                        : 'bg-red-50 border-red-200'
                    }`}>
                      <Users className={`w-8 h-8 ${
                        isOccupied ? 'text-green-500' : 'text-[#FF5A5F]'
                      }`} />
                    </div>
                    <h3 className="text-lg font-bold text-gray-900 mb-2">
                      Meja {table.number}
                    </h3>
                    {isOccupied && (
                      <div className="inline-flex items-center gap-1 px-2 py-1 rounded-full bg-green-100 mb-2">
                        <div className="w-3 h-3 rounded-full bg-green-500"></div>
                        <span className="text-xs font-semibold text-green-700">Terisi</span>
                      </div>
                    )}
                    <div className="flex items-center justify-center gap-1.5 text-gray-600">
                      <Users className="w-4 h-4" />
                      <span className="text-sm font-medium">{table.capacity} orang</span>
                    </div>
                  </div>

                  {/* Card Footer */}
                  <div className="flex border-t border-gray-100">
                    <button
                      onClick={() => showQRCode(table)}
                      className="flex-1 flex items-center justify-center py-3 hover:bg-gray-50 transition-colors border-r border-gray-100"
                      title="Lihat QR Code"
                    >
                      <QrCode className="w-4 h-4 text-[#FF5A5F]" />
                    </button>
                    <button
                      onClick={() => openModal(table)}
                      className="flex-1 flex items-center justify-center py-3 hover:bg-gray-50 transition-colors border-r border-gray-100"
                      title="Edit Meja"
                    >
                      <Edit2 className="w-4 h-4 text-gray-600" />
                    </button>
                    <button
                      onClick={() => deleteTable(table.id)}
                      className="flex-1 flex items-center justify-center py-3 hover:bg-gray-50 transition-colors"
                      title="Hapus Meja"
                    >
                      <Trash2 className="w-4 h-4 text-red-500" />
                    </button>
                  </div>
                </div>
              )
            })
          )}
        </div>
      </main>

      {/* Table Modal */}
      {showModal && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
          <div className="bg-white rounded-2xl p-6 max-w-md w-full">
            <h2 className="text-2xl font-bold mb-6">
              {editingTable ? 'Edit Meja' : 'Tambah Meja'}
            </h2>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Nomor Meja
                </label>
                <input
                  type="text"
                  required
                  value={formData.number}
                  onChange={(e) => setFormData({...formData, number: e.target.value})}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-[var(--color-accent)]"
                  placeholder="Contoh: A1, B2, VIP-1"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Kapasitas (Kursi)
                </label>
                <input
                  type="number"
                  min="1"
                  required
                  value={formData.capacity}
                  onChange={(e) => setFormData({...formData, capacity: e.target.value})}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-[var(--color-accent)]"
                />
              </div>
              <div className="flex gap-2 pt-4">
                <button
                  type="button"
                  onClick={() => setShowModal(false)}
                  className="flex-1 px-4 py-2 border border-gray-200 text-gray-700 rounded-lg font-medium hover:bg-gray-50 transition-colors"
                >
                  Batal
                </button>
                <button
                  type="submit"
                  className="flex-1 px-4 py-2 bg-[#FF5A5F] text-white rounded-lg font-medium hover:bg-[#FF5A5F]/90 transition-colors"
                >
                  {editingTable ? 'Simpan' : 'Tambah'}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}

      {/* QR Code Modal */}
      {showQRModal && selectedQRTable && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
          <div className="bg-white rounded-2xl p-6 max-w-md w-full">
            <h2 className="text-xl font-bold text-gray-900 text-center mb-6">
              QR Code - Meja {selectedQRTable.number}
            </h2>
            
            <div className="bg-gray-50 p-4 rounded-xl mb-6">
              <div className="bg-white p-4 rounded-lg shadow-sm mx-auto w-fit">
                <img 
                  src={`https://api.qrserver.com/v1/create-qr-code/?size=200x200&data=${encodeURIComponent(`https://order.kadaipos.id/${selectedQRTable.barcode || selectedQRTable.id}`)}`}
                  alt="QR Code"
                  className="w-52 h-52"
                />
              </div>
              <div className="mt-4 text-center space-y-2">
                <button
                  onClick={() => {
                    const url = `https://order.kadaipos.id/${selectedQRTable.barcode || selectedQRTable.id}`
                    window.open(url, '_blank')
                  }}
                  className="text-[#FF5A5F] font-semibold text-sm hover:underline"
                >
                  Buka tautan pelanggan
                </button>
                <button
                  onClick={() => {
                    const url = `https://order.kadaipos.id/${selectedQRTable.barcode || selectedQRTable.id}`
                    navigator.clipboard.writeText(url)
                    alert('Link berhasil disalin!')
                  }}
                  className="block w-full text-gray-900 font-semibold text-sm hover:underline"
                >
                  Salin tautan
                </button>
              </div>
            </div>

            <p className="text-sm text-gray-600 text-center mb-6 leading-relaxed">
              Pelanggan bisa scan QR code ini dengan kamera smartphone mereka untuk memesan langsung di meja
            </p>

            <div className="flex gap-3">
              <button
                onClick={() => setShowQRModal(false)}
                className="flex-1 px-4 py-3 bg-gray-100 text-gray-700 rounded-xl font-semibold hover:bg-gray-200 transition-colors"
              >
                Tutup
              </button>
              <button
                onClick={() => {
                  const url = `https://order.kadaipos.id/${selectedQRTable.barcode || selectedQRTable.id}`
                  if (navigator.share) {
                    navigator.share({
                      title: `QR Code Meja ${selectedQRTable.number}`,
                      text: `Pesan di Meja ${selectedQRTable.number}`,
                      url: url
                    })
                  } else {
                    navigator.clipboard.writeText(url)
                    alert('Link berhasil disalin!')
                  }
                }}
                className="flex-1 px-4 py-3 bg-[#FF5A5F] text-white rounded-xl font-semibold hover:bg-[#FF5A5F]/90 transition-colors"
              >
                Bagikan
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
